<script>
import io from 'socket.io-client'
const socket = io(`http://localhost:2021`)
var id = require("date-fns/locale/id");
import downloadExcel from 'vue-json-excel'
import upload from "@/components/Single/UploadsNo.vue";
import axios from "axios";
export default {
  components: {
    upload,
    downloadExcel
  },
  layout:"loan",
  data() {
    return {
      // style
      thbackground: "initial",
      thcolor: "asd",
      // Data datable
      datanya: [],
      checkedItem:[],
      vdata: {
        tanggal:""
      },
      file: "",
        imgs: [],       
        ready:false,
      search: "",
      pilih: { name: "taufik", hobby: "coding" },
      thnya: {},
      err: false,
      checkedItem:[],
      less: [,'_id','no','id_user','file'],
      // modal
      modal: false,
      modalAction: "preview",
      // pagination
      selected: 10,
      pagination: 1,
      temp1: 0,
      temp2: 10,
      orderBy: "",
      filesnya:[],
      orderWith: true,
      // media queries javascript
      mdq: "xlg",
      //kirim
      kirim: {},
      //form
      aksi: "insert",
      formOn: false,
      updateOn: false
    };
  },
  methods: {
       format2(date) {
      return this.$datefns.format(date, "dddd, DD MMMM YYYY", {
        locale: id
      });
    },
        checkedAll() {
            let data = this.td.filter(e=>{
                if(this.checkedItem.includes(e.no)){
                    return e
                }
            })
              this.$router.push(`/laporan/beritaacara2?less=${this.less.toString()}`);
      this.$store.state.print2 = data
      alert("print banyak");
    },
      approve(){
        let fd = new FormData()
        fd.append('data', `update permintaan set tanggal_release='${this.format2(new Date())}', status='Release',mengambil='${this.vdata.mengambil}' where no='${this.pilih.no}'`)//database setting di server
        fd.append('database','loandocument')
        axios.post('http://localhost:8080/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            alert('berhasil di update!')
            this.refreshData()
            this.$forceUpdate()
        })
      },
      kembali(){
           let fd = new FormData()
        fd.append('data', `update permintaan set tanggal_pengembalian='${this.format2(new Date())}', status='Done' where no='${this.pilih.no}'`)//database setting di server
        fd.append('database','loandocument')
        axios.post('http://localhost:8080/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            alert('berhasil di update!')
            this.refreshData()
            this.$forceUpdate()
        })
      },
      downloads(item){
          let fd = new FormData()
          window.location=`http://localhost/${item}`
      },
      deletesDownload(id){
          if(confirm('Yakin menghapus file?')){
              
          let fd = new FormData()
          fd.append('data', `delete from file_upload where id='${id}'`)//database setting di server
          fd.append('database','loandocument')
          axios.post('http://localhost:8080/api/mysql/auto.php', fd
          , {
              headers: {
              Authorization: localStorage.getItem('auth._token.local')
              }
          }).then(res => {
              console.log(res)
              this.refreshfile()
              this.$forceUpdate()
          })
          console.log()
          }
      },
     getFile(e){ this.file=e.target.files[0] },       
        compress(event) {
            // this.imgList=e.target.files;
            if (event.target.files[0].size > 10000000) {
                alert('File yang di upload tidak sesuai kriteria')
            } else {
                if (event.target.files.length > 0) {
                let img = []
                let hasil = []
                let compress = 'null'
                let s
                for (let i = 0; i < event.target.files.length; i++) {
                    this.imgs.push(event.target.files[i])
                    var selectedFile = event.target.files[i]
                    var reader = new FileReader()
                    reader.onload = function(event) {
                    //   img.push(event.target.result)
                    }
                    reader.readAsDataURL(selectedFile)
                    s = event.target.files[i]
                    compress =
                    s.size < 1000000
                        ? 30
                        : s.size < 2000000
                        ? 25
                        : s.size < 3000000
                            ? 20
                            : s.size < 4000000
                            ? 15
                            : 10
                    this.$daycaca.compress(s, compress, data => {
                    this.gambar = data
                    img.push(data)
                    this.$urltofile(data, s.name, s.type).then(res => {
                        hasil.push(res)
                    })
                    })
                }
                console.log('base64',img)
                setTimeout(() => {
                    console.log('file',hasil[0])
                    this.file = hasil[0]
                    this.ready=true
                }, 1000);
                //   this.$emit('upload', hasil)
                }
            }
            },
            inputfile() {
                console.log(this.pilih)
                if(this.pilih.name=='taufik'){
                    alert('pilih salah satu document terlebih dahulu!')
                }else{
                let fd = new FormData();
                fd.append("file", this.file);
                fd.append("id", this.$store.state.users.nip);
                axios
                .post("http://localhost:8080/api/mysql/ld/upload.php", fd, {
                headers: {
                    "content-type": "multipart/form-data"
                }
                })
                .then(res => {
                    let fd = new FormData()
                    fd.append('data', `insert into file_upload (id_permintaan,nama_file,path) values ('${this.pilih.no}','${this.vdata.nama_file}','${res.data}')`)//database setting di server
                    fd.append('database','loandocument')
                    axios.post('http://localhost:8080/api/mysql/auto.php', fd
                    , {
                        headers: {
                        Authorization: localStorage.getItem('auth._token.local')
                        }
                    }).then(res => {
                        console.log(res)
                        alert('upload file berhasil')
                        this.refreshfile()
                        this.$forceUpdate()
                    })
                console.log(res); // get /api/gambar/url.jpg
                });
                }
            },
      insert(){ let fd = new FormData();
      
      fd.append("data", `Insert into permintaan (unit_kerja,nip,pegawai,jabatan,developer,jenis_dokumen,no_dokumen,no_surat,notaris,perihal,desa_kelurahan,keperluan,tanggal_pinjam,tanggal_release,tanggal_pengembalian,status,file,id_user,mengambil) VALUES ('${this.vdata.unit_kerja}','${this.vdata.nip}','${this.vdata.pegawai}','${this.vdata.jabatan}','${this.vdata.developer}','${this.vdata.jenis_dokumen}','${this.vdata.no_dokumen}','${this.vdata.no_surat}','${this.vdata.notaris}','${this.vdata.perihal}','${this.vdata.desa_kelurahan}','${this.vdata.keperluan}','${this.vdata.tanggal_pinjam}','${this.vdata.tanggal_release}','${this.vdata.tanggal_pengembalian}','${this.vdata.status}','${this.vdata.file}','${this.vdata.id_user}','${this.vdata.mengambil}')`);
      fd.append("database", "loandocument");
      
      axios
        .post("http://localhost:8080"+"/api/mysql/auto.php", fd, {
          headers: {
            Authorization: localStorage.getItem("auth._token.local")
          }
        })
        .then(res => {
          
          console.log(res);
          alert('Simpan Data Berhasil!')
          this.refreshData()
        }).catch(err => {
            
            this.refreshData()
          });
    },update(){ let fd = new FormData();
      
      fd.append("data", `UPDATE permintaan SET unit_kerja='${this.vdata.unit_kerja}',nip='${this.vdata.nip}',pegawai='${this.vdata.pegawai}',jabatan='${this.vdata.jabatan}',developer='${this.vdata.developer}',jenis_dokumen='${this.vdata.jenis_dokumen}',no_dokumen='${this.vdata.no_dokumen}',no_surat='${this.vdata.no_surat}',notaris='${this.vdata.notaris}',perihal='${this.vdata.perihal}',desa_kelurahan='${this.vdata.desa_kelurahan}',keperluan='${this.vdata.keperluan}',tanggal_pinjam='${this.vdata.tanggal_pinjam}',tanggal_release='${this.vdata.tanggal_release}',tanggal_pengembalian='${this.vdata.tanggal_pengembalian}',status='${this.vdata.status}',file='${this.vdata.file}',id_user='${this.vdata.id_user}' Where no = '${this.vdata.no}'`);
      fd.append("database", "loandocument");
      
      axios
        .post("http://localhost:8080"+"/api/mysql/auto.php", fd, {
          headers: {
            Authorization: localStorage.getItem("auth._token.local")
          }
        })
        .then(res => {
          
          console.log(res);
          alert('Update Data Berhasil!')
          this.refreshData()
        }).catch(err => {
           
            this.refreshData()
          });
    },remove(){ let fd = new FormData();
      
      fd.append("data", `DELETE FROM permintaan WHERE  no =  '${this.vdata.no}'`);
      fd.append("database", "loandocument");
      if (confirm('Yakin dihapus?')) {
      
      axios
        .post("http://localhost:8080"+"/api/mysql/auto.php", fd, {
          headers: {
            Authorization: localStorage.getItem("auth._token.local")
          }
        })
        .then(res => {
         
          console.log(res);
          alert('Delete Data Berhasil!')
          let keys = Object.keys(this.vdata)
          keys.forEach(key => {
            this.vdata[key] = ''
          })
          this.refreshData()
        }).catch(err => {
            
            let keys = Object.keys(this.vdata)
          keys.forEach(key => {
            this.vdata[key] = ''
          })
            this.refreshData()
          });
          }
    
      }
      ,ambil(item, index) {
      window.scrollTo(0, 0);
      let that = this;
      this.updateOn = true;
      this.formOn = true;
      let clone = this.$_.clone(item);
      // clone["TGL_LHR"] = this.$datefns.format(
      //   new Date(clone["TGL_LHR"]),
      //   "YYYY-MM-dd"
      // );
      console.log(clone);
      this.vdata = clone;
      this.pilih = item;
      this.refreshfile()
    },
    refreshfile(item){
        let fd = new FormData()
        fd.append('data', `select * from file_upload where id_permintaan='${this.pilih.no}'`)//database setting di server
        fd.append('database','loandocument')
        axios.post('http://localhost:8080/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log('fileny',res)
            this.filesnya=res.data
            this.$forceUpdate()
        })
    },
    deletefile(){

    },
     clearInput() {
      this.formOn = true;
      let keys = Object.keys(this.vdata);
      keys.forEach(key => {
        this.vdata[key] = "";
      });
    },
     refreshData() {
      let fd2 = new FormData();
      fd2.append("data", "select no,no_dokumen,tanggal_pinjam,status,unit_kerja,nip,pegawai,jabatan,developer,jenis_dokumen,no_surat,notaris,perihal,desa_kelurahan,keperluan,tanggal_release,mengambil,tanggal_pengembalian,file,id_user from permintaan"); //if empty = *
      fd2.append('table', 'permintaan')
      fd2.append('database', "loandocument")
      fd2.append('aksi', 'select')
      axios
        .post("http://localhost:8080" + "/api/mysql/auto.php", fd2, {
          headers: {
            Authorization: localStorage.getItem("auth._token.local")
          }
        })
        .then(res => {
          console.log("mysql auto", res);
          setTimeout(() => {
            (function() {
              var thElm;
              var startOffset;
              Array.prototype.forEach.call(
                document.querySelectorAll("table th"),
                function(th) {
                  th.style.position = "relative";
                  var grip = document.createElement("div");
                  grip.innerHTML = "&nbsp;";
